"use client";

import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import Editor from "@monaco-editor/react";
import type { Code, AppTheme } from "@/app/page";
import { Skeleton } from "@/components/ui/skeleton";

interface CodeEditorProps {
  code: Code;
  onCodeChange: (newCode: Code) => void;
  theme: AppTheme;
}

export function CodeEditor({ code, onCodeChange, theme }: CodeEditorProps) {
  const handleHtmlChange = (value?: string) => {
    onCodeChange({ ...code, html: value || "" });
  };
  const handleCssChange = (value?: string) => {
    onCodeChange({ ...code, css: value || "" });
  };
  const handleJsChange = (value?: string) => {
    onCodeChange({ ...code, js: value || "" });
  };

  const editorOptions = {
    minimap: { enabled: false },
    fontSize: 14,
    wordWrap: "on" as const,
    scrollBeyondLastLine: false,
    automaticLayout: true,
  };

  const editorTheme = theme === "dark" ? "vs-dark" : "light";

  return (
    <Tabs defaultValue="html" className="w-full h-full flex flex-col">
      <TabsList className="grid w-full grid-cols-3">
        <TabsTrigger value="html">HTML</TabsTrigger>
        <TabsTrigger value="css">CSS</TabsTrigger>
        <TabsTrigger value="js">JavaScript</TabsTrigger>
      </TabsList>
      <div className="flex-1 mt-2 rounded-md border overflow-hidden">
        <TabsContent value="html" className="m-0 h-full">
          <Editor
            height="100%"
            language="html"
            theme={editorTheme}
            value={code.html}
            onChange={handleHtmlChange}
            options={editorOptions}
            loading={<Skeleton className="h-full w-full" />}
          />
        </TabsContent>
        <TabsContent value="css" className="m-0 h-full">
          <Editor
            height="100%"
            language="css"
            theme={editorTheme}
            value={code.css}
            onChange={handleCssChange}
            options={editorOptions}
            loading={<Skeleton className="h-full w-full" />}
          />
        </TabsContent>
        <TabsContent value="js" className="m-0 h-full">
          <Editor
            height="100%"
            language="javascript"
            theme={editorTheme}
            value={code.js}
            onChange={handleJsChange}
            options={editorOptions}
            loading={<Skeleton className="h-full w-full" />}
          />
        </TabsContent>
      </div>
    </Tabs>
  );
}
