"use client";

import { useMemo } from "react";
import { cn } from "@/lib/utils";
import type { Code, Device } from "@/app/page";

interface LivePreviewProps {
  code: Code;
  device: Device;
}

const deviceStyles: Record<Device, string> = {
  desktop: "w-full h-full",
  tablet: "w-[768px] h-[1024px] border-4 border-gray-700 rounded-2xl shadow-xl",
  mobile: "w-[375px] h-[667px] border-4 border-gray-800 rounded-2xl shadow-xl",
};

export function LivePreview({ code, device }: LivePreviewProps) {
  const srcDoc = useMemo(() => {
    // Check if the HTML contains references to /assets/ and adjust the base tag accordingly.
    const hasAssets = code.html.includes("/assets/");
    const baseTag = hasAssets ? `<base href="${window.location.origin}">` : '';

    return `
        <!DOCTYPE html>
        <html lang="en">
        <head>
          <meta charset="UTF-8">
          <meta name="viewport" content="width=device-width, initial-scale=1.0">
          ${baseTag}
          <style>${code.css}</style>
        </head>
        <body>
          ${code.html}
          <script>${code.js}</script>
        </body>
        </html>
      `;
  }, [code]);

  return (
    <div className="w-full h-full bg-muted flex items-center justify-center p-4 overflow-auto rounded-md">
      <div
        className={cn(
          "bg-white transition-all duration-300 ease-in-out",
          deviceStyles[device]
        )}
      >
        <iframe
          key={JSON.stringify(code)}
          srcDoc={srcDoc}
          title="Live Preview"
          sandbox="allow-scripts allow-same-origin"
          className="w-full h-full"
          frameBorder="0"
        />
      </div>
    </div>
  );
}
